
.footers{
	height: 1.6rem;
}
.index-footer{
	border-top: 0.026667rem solid #eee;
	width: 100%;
	height: 1.6rem;
	background-color: #fff;
	position: fixed;
	bottom: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.index-footer svg{
	width: 0.533333rem;
	height: 0.533333rem;
}
.index-footer .in-f-b{
	height: 70%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	text-align: center;
}
.index-footer .in-f-b span{
	font-size: 0.24rem;
}



