
.recgoods{
	/*height: 3.718667rem;*/
	border-bottom: 1px solid #eee;
	background-color: #ffffff;
}
.recgoods #tits{
	background-color: #ffffff;
	padding: 0.266667rem;
	font-size: 0.4rem;
	font-weight: 700;
	border-top: 0.026667rem solid #eee;
	border-bottom: 0.026667rem solid #eee;
	margin-top: 0.266667rem;
}
.list{
	border-bottom: 0.026667rem solid #eee;
}
.left-list{
	width: 20%;
	padding: 0.266667rem;
	float: left;
}
.right-list{
	width: 80%;
	padding: 0.4rem 0.266667rem 0.4rem 0rem;
	float: left;
	color: #666;
}
.right-list h3{
	font-size: 0.4rem;
	font-weight: 700;
}
.r-l-s1{
	margin-top: 0.2rem;
}
.r-l-s1_1{
	margin: 0 0.106667rem;
	color: #ff6000;
}
.r-l-s2{
	margin-top: 0.226667rem;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	display: -wekit-flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}
.r-l-s2d span + span::before{
	margin: 0 .08rem;
	color: #ddd;
	content: "/";
}
.r-l-s2f span + span::before{
	margin: 0.08rem;
	content: '/';
	color: #ddd;
}
.r-l-s2tm{
	color: #2395ff;
}
.youh{
	margin-top: 0.2rem;
	border-top: 1px solid #eee;
	color: #939393;
	padding-top: 0.133333rem;
}
.youh .youht{
	font-size: 0.266667rem;
	margin-top: 0.066667rem;
}
.youh .youht .youht_i{
	font-style: normal;
	background-color: #70bc46;
	border-color: #70bc46;
	color: #fff;
	margin-right: 0.266667rem
}
.youh .youhb{
	font-size: 0.266667rem;
	margin-top: 0.213333rem;
}
.youh .youhb .youhb_i{
	font-style: normal;
	background-color: #f07373;
	color: #fff;
	border-color: #f07373;
	margin-right: 0.266667rem;
}
.clear{
	clear: both;
}



